            </div>
          <!-- </router-link>       -->
        </div>

      </div>
    </div>
  <!-- </div> -->
</template>
    
<script>
  import { mapActions} from 'vuex'

  export default {
    data() {
      return {
        isAccept: false
      }
    },
    methods: {
      ...mapActions([
        "increaseBackgroundId",
        "newTest"
      ]),
      changeAccept() {
        this.isAccept = !this.isAccept
      },
      toGame(){
        this.increaseBackgroundId()
        this.newTest()
      }

    },
  }
</script>

