<template>
  <div class="continue-wrapper">
    <div class="continue-page">
      <div class="continue-text">
        Продолжить выполнение сценария или начать сначала?
      </div>
      <div class="footer-section">
        <div
          @click="checkContinueTest(true)"
          class="footer-button"
        >
          Продолжить
        </div>
        <div
          @click="checkContinueTest(false)"
          class="footer-button"
        >
          Начать с начала
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    methods: {
      checkContinueTest(isContinue){
        this.$emit('checkContinueTest', isContinue)
      }
    },
  }
</script>

