<template>
  <div 
    :style="[currentBackground, computedHeight]"
    class="main-wrapper"    
  >
    <router-view/>
  </div>

  <!-- <div 
    class="main-wrapper-mini"
  >
    <router-view/>
  </div> -->

</template>

<script>
import { mapGetters, mapActions} from 'vuex'

export default {
  name: 'App',
  data() {
    return {
      dataHeight: null
    }
  },
  computed: {
    ...mapGetters([
        "currentBackground",
        "windowHeight"
      ]),
      computedHeight(){
        return {'min-height': this.dataHeight + "px"}
      }
  },
  methods: {
    ...mapActions([
        "setWindowWidth",
        "setWindowHeight"
    ]),
    setDataHeight(){
      console.log();
      this.dataHeight = window.innerHeight
    }
  },
  mounted() {
    this.setWindowWidth(document.body.clientWidth)
    this.setDataHeight()
    const x = this.setWindowWidth
    const y = this.setDataHeight
    window.addEventListener("resize", () => {
      x(document.body.clientWidth)
      y()
    })
       

  }
}
</script>

