import { createStore } from 'vuex'
import axios from 'axios'
import router from '@/router'

export default createStore({
  state: {
    serverURL: "https://dev1.med-game.ru/api/v1",
    //Первый
    // testId: 11,
    // startQuestionId: 563,
    // questionId: 563,
    
    //Второй
    testId: 25,
    startQuestionId: 813,
    questionId: 813,
    
    //тестовый
    // testId: 28,
    // startQuestionId: 969,
    // questionId: 969,

    questionStack: [],
    pointsStack: [],
    parentId: 0,
    infoText: "",
    questionText: "",
    answerTransitions: [],
    currentTransitionId: 0,
    
    
    backgroundId: 0,
    backgroundList: [
      //Первый
      // "back1.jpg",
      // "back2.jpg",
      // "back3.jpg",
      // "back4.jpg",
      // "back5.jpg",
      // "back6.jpg",
      // "back7.jpg",
      // "back8.jpg",
      // "back9.jpg"
      //Второй
      "back2_1.jpg",
      "back2_2.jpg",
      "back2_3.jpg",
      "back2_4.jpg",
      "back2_5.jpg",
      "back2_6.jpg",
      "back2_7.jpg",
      "back2_8.jpg",
      "back2_9.jpg",
      "back2_10.jpg"
    ],
    //Первый
    // backgroundStart: "backStart.jpg",
    //Второй
    backgroundStart: "backStart2.jpg",
    
    resumeText: "",
    nodePoints: 0,
    windowWidth: 0,
    windowHeight: 0
  },
  getters: {
    testId(state){
      return state.testId
    },
    questionId(state){
      return state.questionId
    },
    // bodyData(state){
    //   return state.bodyData
    // },
    parentId(state){
      return state.parentId
    },
    infoText(state){
      return state.infoText
    },
    questionText(state){
      return state.questionText
    },
    answerTransitions(state){
      return state.answerTransitions
    },
    questionStack(state){
      return state.questionStack
    },
    startQuestionId(state){
      return state.startQuestionId
    },
    serverURL(state){
      return state.serverURL      
    },
    currentBackground(state){
      if (router.currentRoute.value.name === "start") {
        return {backgroundImage: 'url(' + require('@/assets/images/' + state.backgroundStart) + ')'}
      }
      else {
        if (state.windowWidth < 480) {
          return {backgroundImage: "none"}
        }
        return {backgroundImage: 'url(' + require('@/assets/images/' + state.backgroundList[state.backgroundId]) + ')'}
      }
    },
    resumeText(state){
      return state.resumeText
    },    
    nodePoints(state){
      return state.nodePoints
    },
    pointsStack(state){
      return state.pointsStack
    },
    windowWidth(state){
      return state.windowWidth
    },
    windowHeight(state){
      return state.windowHeight
    },

  },
  mutations: {
    setDataQuestion(state, data){
      // state.parentId = data.body.parentId
      state.infoText = data.body.infoText
      state.questionText = data.body.questionText
      state.answerTransitions = data.body.answerTransitions
      if (data.body.answerTransitions.length === 1) {
        state.currentTransitionId = data.body.answerTransitions[0].transitionId
      }
      else{
        state.currentTransitionId = 0
      }
      state.resumeText = data.body.resumeText ? data.body.resumeText : "",
      state.nodePoints = data.body.nodePoints ? data.body.nodePoints : 0
    },
    setQuestionId(state, questionId){
      state.questionId = questionId
    },
    setCurrentTransitionId(state, transitionId){
      state.currentTransitionId = transitionId
    },
    addToStack(state, id){
      state.questionStack.push(id)
    },
    removeFromStack(state){
      state.questionStack.pop()
      state.questionId = state.questionStack[state.questionStack.length - 1]
    },
    addToLocalStorage(state){
      localStorage.setItem(`questionList${state.testId}`, JSON.stringify(state.questionStack))
    },
    continueTest(state, questionList){
      state.questionStack = questionList
      state.questionId = questionList[questionList.length - 1]
      
      const pointsStackLS = localStorage.getItem(`pointsList${state.testId}`)
      if (pointsStackLS) {
        state.pointsStack = JSON.parse(pointsStackLS)
      }
      else{
        state.pointsStack = []
      }      
    },
    newTest(state){
      state.questionStack = [state.startQuestionId]
      state.questionId = state.startQuestionId
      state.pointsStack = []
      router.push('/game')
    },
    increaseBackgroundId(state){
      if (state.backgroundId === state.backgroundList.length - 1) {
        state.backgroundId = 0
      }
      else {
        state.backgroundId += 1
      }      
    },
    decreaseBackgroundId(state){
      if (state.backgroundId === 0) {
        state.backgroundId = state.backgroundList.length - 1
      }
      else {
        state.backgroundId -= 1
      }      
    },
    addPoints(state){
      state.pointsStack.push(state.nodePoints)
    },
    removePoints(state){
      state.pointsStack.pop()
    },
    addPointsToLS(state){
      localStorage.setItem(`pointsList${state.testId}`, JSON.stringify(state.pointsStack))
    },
    setWindowWidth(state, width){
      state.windowWidth = width
    },    
    setWindowHeight(state, height){
      state.windowHeight = height
    },

  },

  actions: {

    GET_QUESTION_DATA({state, commit}, changePoints){
      axios({
        method: 'get',
        url: `${state.serverURL}/question/${state.questionId}`,
      })
      .then((questionData) => {
        commit("setDataQuestion", questionData.data.data)
      })
      .then(() => {
        if (changePoints === "increase") {
          commit("addPoints")
          commit("addPointsToLS")
        }
        else if (changePoints === "decrease") {
          commit("removePoints")
          commit("addPointsToLS")
        }
      })
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },

    nextQuestion({commit, dispatch, state}){
      if (state.currentTransitionId) {
        commit('addToStack', state.currentTransitionId)
        commit('addToLocalStorage')
        commit('setQuestionId', state.currentTransitionId)
        commit('increaseBackgroundId')
        return dispatch('GET_QUESTION_DATA', "increase")
      }
      else {
        console.log("Не выбран ответ")
      }
    },
    previousQuestion({commit, dispatch}){
      commit('removeFromStack')
      commit('addToLocalStorage')
      commit('decreaseBackgroundId')
      return dispatch('GET_QUESTION_DATA', "decrease")
    },

    setCurrentTransitionId({commit}, transitionId){
      commit("setCurrentTransitionId", transitionId)
    },

    continueTest({commit, dispatch}, questionList){
      commit("continueTest", questionList)
      return dispatch('GET_QUESTION_DATA')
    },
    newTest({commit, dispatch}){
      commit("newTest")
      commit('addToLocalStorage')
      // commit('increaseBackgroundId')
      return dispatch('GET_QUESTION_DATA', "increase")
    },
    increaseBackgroundId({commit}){
      commit("increaseBackgroundId")
    },
    decreaseBackgroundId({commit}){
      commit("decreaseBackgroundId")
    },
    setWindowWidth({commit}, width){
      commit("setWindowWidth", width)
    },
    setWindowHeight({commit}, height){
      commit("setWindowHeight", height)
    }    

  },
  modules: {

  }
})
